import React from "react";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from "@mui/material";

const ConfirmationDialog = ({ open, onClose, onConfirm }) => {
  return (
    <Dialog open={open} onClose={onClose}>
      <DialogTitle>確認刪除</DialogTitle>
      <DialogContent>
        <DialogContentText>您確定清除全部測試結果嗎？</DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button
          onClick={onConfirm}
          color="primary"
          variant="contained"
          sx={{ marginRight: 1 }}
        >
          確認
        </Button>
        <Button onClick={onClose} color="error" variant="outlined">
          取消
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default ConfirmationDialog;
