import { List, Paper, TextField, Typography } from "@mui/material";
import { Container } from "@mui/system";
import { useState } from "react";
import { decompose } from "./asset/cangjie";
import SearchIcon from "@mui/icons-material/Search";

function SearchCode() {
  const [sentence, setSentence] = useState("");
  const [instructions, setInstructions] = useState([]);

  const handleChange = (e) => {
    setSentence(e.target.value);
    setInstructions(decompose(e.target.value));
  };

  return (
    <Container maxWidth="lg">
      <Paper variant="outlined" sx={{ p: 2, mt: 2 }}>
        <TextField
          label="查詢倉頡碼"
          value={sentence}
          onChange={handleChange}
          fullWidth
          InputProps={{
            startAdornment: <SearchIcon color="disabled" sx={{ mr: 2 }} />,
          }}
        />
        <List sx={{ display: "flex", flexWrap: "wrap", gap: 2 }}>
          {instructions.map((instruction, index) => (
            <Typography key={index} width={120}>
              <b>{instruction.word}</b>: {instruction.codes}
            </Typography>
          ))}
          {sentence !== "" && instructions.length === 0 && (
            <Typography>未找到倉頡碼</Typography>
          )}
        </List>
      </Paper>
    </Container>
  );
}

export default SearchCode;
