const accurateCount = (article, text) => {
  return [...text].reduce(
    (acc, char, idx) => acc + (char === article.text[idx] ? 1 : 0),
    0
  );
};

const calculateAccuracy = (article, text) => {
  const typed = [...text];
  if (typed.length === 0) return 0;
  else return (accurateCount(article, typed) / typed.length) * 100;
};

const calculateWPM = (article, text, startTime, endTime) => {
  return (accurateCount(article, text) / ((endTime - startTime) / 1000)) * 60;
};

export { calculateAccuracy, calculateWPM };
