import React, { useState } from "react";
import {
  Button,
  IconButton,
  Typography,
  List,
  ListItem,
  ListItemText,
  Box,
  Card,
  CardContent,
  Pagination,
} from "@mui/material";
import ConfirmationDialog from "./ConfirmationDialog";
import DeleteIcon from "@mui/icons-material/Delete";

const RECORD_PER_PAGE = 4;

const RecordList = ({ results, handleSetResult }) => {
  const [openConfirm, setOpenConfirm] = useState(false);
  const [page, setPage] = useState(1);
  const totalPage = Math.ceil(results.length / RECORD_PER_PAGE);

  const handleChange = (event, value) => setPage(value);

  const clearResults = () => handleSetResult([]);

  const deleteResult = (index) => {
    handleSetResult(results.filter((_, i) => i !== index));
  };

  const handleDeleteAll = () => {
    setOpenConfirm(false);
    clearResults();
    localStorage.setItem("results", "[]");
  };

  const displayDetail = (result) => {
    const wpm = result.wpm === Infinity ? "無限" : result.wpm.toFixed(0);
    const display = result.isTimeLimit
      ? [
          `⏱️限時: ${result.timeLimit}秒`,
          `⌨️字速: ${wpm}/分鐘`,
          `🎯準確度: ${Math.floor(result.accuracy).toFixed(0)}%`,
        ]
      : [
          // prettier-ignore
          `🕑已用: ${((result.endTime - result.startTime) / 1000).toFixed(1)}秒`,
          `📝字數: ${result.total}隻`,
          `⌨️字速: ${wpm}/分鐘`,
        ];
    return display.join(" | ");
  };

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        gap: 1,
      }}
    >
      <Box
        display="flex"
        justifyContent="space-between"
        alignItems="center"
        sx={{ width: "100%" }}
      >
        <Typography variant="h5">📒過去測試結果</Typography>
        {results.length > 0 && (
          <Button onClick={() => setOpenConfirm(true)}>🧹清除全部</Button>
        )}
      </Box>

      <List sx={{ width: "100%" }} disablePadding>
        {results.length > 0 ? (
          results
            .sort((a, b) => b.endTime - a.endTime)
            .slice((page - 1) * RECORD_PER_PAGE, page * RECORD_PER_PAGE)
            .map((result, index) => (
              <ListItem key={index} disableGutters>
                <Card sx={{ width: "100%" }}>
                  <CardContent sx={{ display: "flex" }}>
                    <ListItemText
                      primary={
                        <Typography variant="h6">{result.name}</Typography>
                      }
                      secondary={
                        <Typography variant="body2" color="text.secondary">
                          {`${new Date(result.endTime).toLocaleString()}`}
                          <br />
                          {displayDetail(result)}
                        </Typography>
                      }
                    />
                    <Box
                      sx={{
                        display: "flex",
                        flexDirection: "column",
                        justifyContent: "center",
                      }}
                    >
                      <IconButton onClick={() => deleteResult(index)}>
                        <DeleteIcon />
                      </IconButton>
                    </Box>
                  </CardContent>
                </Card>
              </ListItem>
            ))
        ) : (
          <Typography>沒有，請選擇文章</Typography>
        )}
      </List>
      {results.length > RECORD_PER_PAGE && (
        <Pagination
          count={totalPage}
          showFirstButton
          showLastButton
          page={page}
          onChange={handleChange}
        />
      )}
      <ConfirmationDialog
        open={openConfirm}
        onClose={() => setOpenConfirm(false)}
        onConfirm={handleDeleteAll}
      />
    </Box>
  );
};

export default RecordList;
