import React, { useState } from "react";
import { TextField, Typography, Box, Checkbox } from "@mui/material";

const SettingsPanel = ({ settings, setSettings }) => {
  const [error, setError] = useState(false);

  const handleSettingChange = (field, value) => {
    const newSettings = { ...settings, [field]: value };
    setSettings(newSettings);
    localStorage.setItem("settings", JSON.stringify(newSettings));
  };

  const handleTimeLimitChange = (e) => {
    const timeLimit = Number(e.target.value);
    if (timeLimit > 0 && Number.isInteger(timeLimit)) {
      handleSettingChange("timeLimit", timeLimit);
      setError(false);
    } else {
      setError(true);
    }
  };

  return (
    <Box marginBottom={2}>
      <Typography variant="h5" gutterBottom>
        ⚙️設定
      </Typography>
      <Box display="flex" alignItems="center">
        <Checkbox
          checked={settings.isTimeLimit}
          onClick={(e) => handleSettingChange("isTimeLimit", e.target.checked)}
        />
        <TextField
          label="限時 (秒)"
          type="number"
          variant="standard"
          onChange={handleTimeLimitChange}
          error={error}
          helperText={error ? "數字必須大於0，必需為整數" : ""}
          defaultValue={settings.timeLimit}
          disabled={!settings.isTimeLimit}
        />
      </Box>
    </Box>
  );
};

export default SettingsPanel;
