import React, { useState } from "react";
import { Container, Typography, Box } from "@mui/material";
import SearchCode from "./SearchCode";
import ArticleSelection from "./ArticleSelection";
import TypingArea from "./TypingArea";
import SettingsPanel from "./SettingsPanel";
import useDocumentTitle from "../../hooks/useDocumentTitle";
import RecordList from "./RecordList";

function Typer() {
  const [selectedArticle, setSelectedArticle] = useState(null);
  const [settings, setSettings] = useState(
    JSON.parse(localStorage.getItem("settings")) || {
      isTimeLimit: false,
      timeLimit: 60,
    }
  );
  const [results, setResults] = useState(
    JSON.parse(localStorage.getItem("results")) || []
  );
  useDocumentTitle("Typer");

  const handleSetResult = (updatedResults) => {
    setResults(updatedResults);
    localStorage.setItem("results", JSON.stringify(updatedResults));
  };

  const handleArticleSelect = (article) => {
    setSelectedArticle(article);
  };

  const handleBack = () => {
    setSelectedArticle(null);
  };

  return (
    <Container>
      <Typography variant="h4" gutterBottom textAlign="center">
        倉頡打字練習
      </Typography>
      {selectedArticle ? (
        <TypingArea
          settings={settings}
          article={selectedArticle}
          handleBack={handleBack}
          addResult={(result) => handleSetResult([result, ...results])}
        />
      ) : (
        <>
          <SearchCode />
          <Box
            marginTop={1}
            display="flex"
            flexDirection={{ xs: "column", md: "row" }}
          >
            <Box flex={1} sx={{ padding: 1 }}>
              <ArticleSelection onSelect={handleArticleSelect} />
            </Box>
            <Box flex={1} sx={{ padding: 1, width: "100%" }}>
              <SettingsPanel settings={settings} setSettings={setSettings} />
              <RecordList results={results} handleSetResult={handleSetResult} />
            </Box>
          </Box>
        </>
      )}
    </Container>
  );
}

export default Typer;
