import React, { useState } from "react";
import {
  List,
  ListItemButton,
  ListItemText,
  Typography,
  Box,
  Pagination,
} from "@mui/material";
import articles from "./asset/articles";
import Divider from "@mui/material/Divider";

const ARTICLE_PER_PAGE = 5;

const ArticleSelection = ({ onSelect }) => {
  const [page, setPage] = useState(1);
  const totalPage = Math.ceil(articles.length / ARTICLE_PER_PAGE);
  const handleChange = (event, value) => setPage(value);

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        gap: 1,
      }}
    >
      <Typography variant="h5" align="center">
        📰文章選擇
      </Typography>
      <List
        disablePadding
        sx={{
          width: "100%",
          borderRadius: 2,
          border: "1px solid",
          borderColor: "divider",
          backgroundColor: "background.paper",
        }}
      >
        {articles
          .sort((a, b) => [...a.text].length - [...b.text].length)
          .slice((page - 1) * ARTICLE_PER_PAGE, page * ARTICLE_PER_PAGE)
          .map((article, index) => (
            <div key={index}>
              <ListItemButton onClick={() => onSelect(article)}>
                <ListItemText
                  primary={
                    <Typography variant="h6">{article.title}</Typography>
                  }
                  secondary={
                    <Typography variant="body2" color="text.secondary">
                      {`${[...article.text].length} 字`}
                    </Typography>
                  }
                />
              </ListItemButton>
              <Divider />
            </div>
          ))}
      </List>
      {articles.length > ARTICLE_PER_PAGE && (
        <Pagination
          count={totalPage}
          showFirstButton
          showLastButton
          page={page}
          onChange={handleChange}
        />
      )}
    </Box>
  );
};

export default ArticleSelection;
