import React, { useState, useEffect } from "react";
import {
  Button,
  Typography,
  TextField,
  Grid,
  Menu,
  Paper,
  Box,
} from "@mui/material";
import { decompose } from "./asset/cangjie";
import { calculateAccuracy, calculateWPM } from "./statistics";

const TypingArea = ({ settings, article, handleBack, addResult }) => {
  const [startTime, setStartTime] = useState(null);
  const [intervalObject, setIntervalObject] = useState();
  const [highlightedText, setHighlightedText] = useState("");
  const [anchorEl, setAnchorEl] = useState(null);
  const [timer, setTimer] = useState(settings.timeLimit);
  const [inputText, setInputText] = useState("");
  const [isInputActive, setIsActive] = useState(true);
  const [result, setResult] = useState();
  const wpm = isInputActive
    ? calculateWPM(article, inputText, startTime, Date.now())
    : result.wpm;
  const accuracy = isInputActive
    ? calculateAccuracy(article, inputText)
    : result.accuracy;

  useEffect(() => {
    return () => {
      if (intervalObject !== undefined) clearInterval(intervalObject);
    };
    // eslint-disable-next-line
  }, []);

  const handleReset = () => {
    setInputText("");
    setTimer(settings.timeLimit);
    setIsActive(false);
    handleBack();
  };

  const endGame = () => {
    const result = {
      startTime: startTime,
      endTime: Date.now(),
      name: article.title,
      wpm: calculateWPM(article, inputText, startTime, Date.now()),
      typed: [...inputText].length,
      total: [...article.text].length,
      accuracy: calculateAccuracy(article, inputText),
      isTimeLimit: settings.isTimeLimit,
      timeLimit: settings.timeLimit,
    };
    addResult(result);
    setResult(result);
    setIsActive(false);
    clearInterval(intervalObject);
  };

  const startTimer = () => {
    const interval = setInterval(() => {
      setTimer((prevTimer) => prevTimer - 1);
    }, 1000);

    setIntervalObject(interval);
  };

  const handleInputChange = (e) => {
    if (startTime === null) {
      setStartTime(Date.now());
      if (settings.isTimeLimit) {
        startTimer();
      }
    }
    setInputText(e.target.value);
  };

  const handleMouseUp = () => {
    const lastHighlightedElement =
      window.getSelection().focusNode.parentElement;
    if (lastHighlightedElement.nodeName === "SPAN") {
      setAnchorEl(lastHighlightedElement);
      setHighlightedText(window.getSelection().toString());
    } else {
      setHighlightedText("");
    }
  };

  const coloredText = [...article.text].map((char, idx) => {
    const color =
      inputText[idx] === undefined
        ? "black"
        : inputText[idx] === char
        ? "green"
        : "red";
    return (
      <span key={idx} style={{ color }}>
        {char}
      </span>
    );
  });

  const cangjieCode = decompose(highlightedText);

  const displayHeaders = () => {
    const headers = [];
    if (isInputActive && settings.isTimeLimit)
      headers.push([`⏱️剩餘 ${timer} 秒`]);
    if (!isInputActive && !settings.isTimeLimit)
      // prettier-ignore
      headers.push([`🕑已用: ${((result.endTime - result.startTime) / 1000).toFixed(1)}秒`,]);

    headers.push(
      `⌨️字速: ${wpm.toFixed(0)}/分鐘`,
      `📝已輸入: ${[...inputText].length}/${[...article.text].length} 隻字`
    );

    if (settings.isTimeLimit)
      headers.push(`🎯準確度:${Math.floor(accuracy).toFixed(0)}%`);
    return headers.join(" | ");
  };

  useEffect(() => {
    document.addEventListener("mouseup", handleMouseUp);
    return () => document.removeEventListener("mouseup", handleMouseUp);
  }, []);

  useEffect(() => {
    if (
      isInputActive &&
      ((settings.isTimeLimit && timer === 0) || inputText === article.text)
    ) {
      endGame();
    }
    // eslint-disable-next-line
  }, [isInputActive, timer, inputText, article.text]);

  return (
    <>
      <Box display="flex" alignItems="center" gap={1} marginBottom={1}>
        <Button variant="contained" color="primary" onClick={handleReset}>
          返回主頁
        </Button>
        <Typography variant="h6" gutterBottom margin={0}>
          {displayHeaders()}
        </Typography>
      </Box>
      <Grid container spacing={2}>
        <Grid item xs={12} md={6}>
          <Paper
            elevation={2}
            style={{
              padding: "16px",
              backgroundColor: "#f9f9f9",
            }}
          >
            {coloredText}
          </Paper>
        </Grid>

        <Grid item xs={12} md={6}>
          <TextField
            fullWidth
            multiline
            minRows={12}
            variant="outlined"
            value={inputText}
            onChange={handleInputChange}
            disabled={!isInputActive}
          />
        </Grid>
      </Grid>
      <Menu anchorEl={anchorEl} open={cangjieCode.length > 0}>
        <Box paddingX={1}>
          {cangjieCode.map((instruction, index) => (
            <Typography key={index}>
              <b>{instruction.word}</b>: {instruction.codes}
            </Typography>
          ))}
        </Box>
      </Menu>
    </>
  );
};

export default TypingArea;
